.header-primary h1 {
  margin-block: .5em;
}

.stepper-input {
  border: 2px solid #201d1f;
  height: 1.25rem;
  display: flex;
}

.stepper-input > * {
  box-sizing: content-box;
}

.stepper-input > :not(:last-child) {
  border-right: 2px solid #201d1f;
}

.stepper-input__input {
  width: 100%;
}

.stepper-input__container {
  align-items: center;
  width: 1.25rem;
  display: flex;
}

.stepper-input__button {
  background-color: #0000;
  border: none;
  outline: none;
  justify-content: center;
  align-items: center;
  width: 1.25rem;
  padding: 0;
  display: flex;
}

.cart-block {
  max-height: auto;
  flex-direction: column;
  gap: 1em;
  width: 100%;
  display: flex;
}

.cart-block__header p, .cart-block__footer p {
  text-transform: uppercase;
  font-size: var(--fs-4);
  color: #201d1f;
  margin: 0;
  font-weight: 700;
}

.cart-block__header {
  display: none;
}

.cart-block__footer {
  box-sizing: border-box;
  border: 2px solid #201d1f;
  grid-template-columns: 1fr auto;
  padding: 1em;
  display: grid;
}

.cart-block__main {
  flex-direction: column;
  display: flex;
  overflow-x: hidden;
  overflow-y: scroll;
}

.cart-block__row {
  box-sizing: border-box;
  grid-template-rows: 1fr;
  grid-template-columns: 1fr auto;
  padding: 1em 0;
  display: grid;
}

.cart-block__row:not(:last-child) {
  border-bottom: 2px solid #201d1f;
}

.cart-block__cell {
  align-items: end;
  gap: .5em;
  max-height: 100%;
  display: flex;
}

.cart-block__cell p {
  font-size: var(--fs-5);
  margin: 0;
}

.cart-block__desc figure {
  width: 100%;
  max-width: 5.6em;
  height: 100%;
}

.cart-block__desc figure img {
  object-fit: cover;
  width: 100%;
  height: 100%;
}

.cart-block__desc div {
  flex-direction: column;
  justify-content: space-between;
  height: 100%;
  display: flex;
}

.cart-block__desc div .title {
  color: #000;
}

.cart-block__desc div .desc {
  color: #201d1f;
  display: block;
}

.cart-block__desc div .price {
  display: block;
}

.cart-block__price, .cart-block__total {
  display: none;
}

.cart-coupon {
  flex-direction: column;
  align-items: flex-start;
  gap: 1em;
  padding-block: 1em;
  display: flex;
}

.cart-coupon__coupon {
  flex-direction: column;
  display: flex;
}

.cart-coupon__coupon p {
  font-size: var(--fs-5);
  color: #201d1f;
  margin: 0;
}

.cart-coupon__coupon form {
  flex-direction: column;
  gap: 1em;
  display: flex;
}

.cart-coupon__coupon form .btn {
  display: none;
}

.cart-coupon__coupon .form-control:after {
  background-color: #ff56b1;
}

