.stepper-input {
  display: flex;
  border: 2px solid $black;
  height: 2rem;
  > * {
    &:not(:last-child) {
      border-right: 2px solid $black;
    }
  }
}

.stepper-input__input {
  width: 100%;
}

.stepper-input__container {
  width: 2rem;
  display: flex;
  align-items: center;
}

.stepper-input__button {
  align-items: center;
  display: flex;
  justify-content: center;
  width: 2rem;
  background-color: transparent;
  border: none;
  outline: none;
}

.cart-block {
  display: flex;
  flex-direction: column;
  width: 100%;
  max-height: 30em;
  gap: 0;
  border: 2px solid $black;
  > *:not(:last-child) {
    border-bottom: 2px solid $black;
  }
}

.cart-block__header,
.cart-block__footer {
  p {
    font-size: var(--fs-4);
    font-weight: $fw-7;
    color: $black;
    margin: 0;
  }
}

.cart-block__header {
  box-sizing: border-box;
  display: grid;
  grid-template-columns: 4fr 2fr 2fr 1fr;
  padding: 1em 2em;
}

.cart-block__footer {
  box-sizing: border-box;
  display: grid;
  grid-template-columns: 8fr 1fr;
  padding: 1em 2em;
  border: none;
}

.cart-block__main {
  display: flex;
  flex-direction: column;
  overflow-y: scroll;
  overflow-x: hidden;
}

.cart-block__row {
  box-sizing: border-box;
  display: grid;
  grid-template-columns: 4fr 2fr 2fr 1fr;
  grid-template-rows: 1fr;
  padding: 1em 2em;

  &:not(:last-child) {
    border-bottom: 2px solid $black;
  }
}

.cart-block__cell {
  max-height: 100%;
  display: flex;
  align-items: center;
  gap: 0.5em;
  p {
    font-size: var(--fs-5);
    margin: 0;
  }
}

.cart-block__desc {
  figure {
    width: 100%;
    height: 100%;
    max-width: 5.6em;
    img {
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
  }
  div {
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    .title {
      color: $dark;
    }
    .desc {
      color: $black;
      display: none;
    }
    .price {
      display: none;
    }
  }
}

.cart-coupon {
  display: flex;
  flex-direction: row;
  align-items: flex-end;
  justify-content: space-between;
  gap: 1em;
  padding-block: 1em;

  .btn {
    flex-shrink: 0;
  }
}

.cart-coupon__coupon {
  display: flex;
  flex-direction: row;
  align-items: flex-end;
  gap: 1em;
  p {
    text-transform: uppercase;
    font-size: var(--fs-4);
    font-weight: $fw-7;
    color: $black;
    margin: 0;
  }
  form {
    display: flex;
    flex-direction: row;
    gap: 1em;

    .btn {
      display: block;
    }
  }
}
