@import "variables";

.header-primary {
  h1 {
    margin-block: 0.5em;
  }
}

.stepper-input {
  display: flex;
  border: 2px solid $black;
  height: 1.25rem;
  > * {
    box-sizing: content-box;
    &:not(:last-child) {
      border-right: 2px solid $black;
    }
  }
}

.stepper-input__input {
  width: 100%;
}

.stepper-input__container {
  width: 1.25rem;
  display: flex;
  align-items: center;
}

.stepper-input__button {
  align-items: center;
  display: flex;
  justify-content: center;
  width: 1.25rem;
  padding: 0;
  background-color: transparent;
  border: none;
  outline: none;
}

.cart-block {
  display: flex;
  flex-direction: column;
  width: 100%;
  max-height: auto;
  gap: 1em;
}

.cart-block__header,
.cart-block__footer {
  p {
    text-transform: uppercase;
    font-size: var(--fs-4);
    font-weight: $fw-7;
    color: $black;
    margin: 0;
  }
}

.cart-block__header {
  display: none;
}

.cart-block__footer {
  box-sizing: border-box;
  display: grid;
  grid-template-columns: 1fr auto;
  padding: 1em 1em;
  border: 2px solid $black;
}

.cart-block__main {
  display: flex;
  flex-direction: column;
  overflow-y: scroll;
  overflow-x: hidden;
}

.cart-block__row {
  box-sizing: border-box;
  display: grid;
  grid-template-columns: 1fr auto;
  grid-template-rows: 1fr;
  padding: 1em 0;

  &:not(:last-child) {
    border-bottom: 2px solid $black;
  }
}

.cart-block__cell {
  max-height: 100%;
  display: flex;
  align-items: end;
  gap: 0.5em;
  p {
    font-size: var(--fs-5);
    margin: 0;
  }
}

.cart-block__desc {
  figure {
    width: 100%;
    height: 100%;
    max-width: 5.6em;
    img {
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
  }
  div {
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    .title {
      color: $dark;
    }
    .desc {
      color: $black;
      display: block;
    }
    .price {
      display: block;
    }
  }
}

.cart-block__price,
.cart-block__total {
  display: none;
}

.cart-coupon {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  gap: 1em;
  padding-block: 1em;
}

.cart-coupon__coupon {
  display: flex;
  flex-direction: column;
  p {
    font-size: var(--fs-5);
    color: $black;
    margin: 0;
  }
  form {
    display: flex;
    flex-direction: column;
    gap: 1em;

    .btn {
      display: none;
    }
  }
  .form-control {
    &::after {
      background-color: $primary-500;
    }
  }
}

@include sm {
  @import "my-cart-sm";
}
